// pages/revisionFloat/dataHelpers.js

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheck } from '@fortawesome/free-solid-svg-icons';
import { faXmark } from '@fortawesome/free-solid-svg-icons';

const CheckmarkCell = () => (
  <FontAwesomeIcon
    icon={faCheck}
    className="green-icon"
  />
);

const CrossmarkCell = () => (
<FontAwesomeIcon icon={faXmark}  className='cross-icon' />
);


export const getCardData = (droppedObjectData) => {
  if (!droppedObjectData || !droppedObjectData.draggedData) {
    return null;
  }

  const item = droppedObjectData.draggedData;

  let cardData = {
    title: item.Title || "N/A",
    type: item.Type || "N/A",
    "Maturity State": item["Maturity State"] || "N/A",
    owner: item.Owner || "N/A",
    "Collaborative Space Title": item["Collaborative Space Title"] || "N/A",
    Description: item.Description || "N/A",
    "Dropped Revision": item["Dropped Revision"] || "N/A",
    "Latest Released Revision": item["Latest Released Revision"] || "N/A",
    "CAD Format": item["CAD Format"] || "N/A",
    imageURL: item.imageURL || "https://oi000186152-us1-space.3dexperience.3ds.com/enovia/snresources/images/icons/large/I_VPMNavProduct108x144.png", // You might want a placeholder image URL
  };

  if (item.Type !== "Document") {
    cardData.EIN = item.EIN || "N/A";
    cardData["CAD Format"] = item["CAD Format"] || "N/A";
  }
  return cardData;
};

export const getTableData = (droppedObjectData) => {
  if (
    !droppedObjectData ||
    !droppedObjectData.parentDetails ||
    !droppedObjectData.parentDetails.data
  ) {
    console.warn("getTableData: parentDetails.data is undefined");
    return [];
  }

  const item = droppedObjectData.draggedData;
  const parents = droppedObjectData.parentDetails.data;
  

  return parents.map((parent) => {
    let rowData = {
      EIN: parent.EIN || "N/A",
      Title: parent.Title || "N/A",
      Description: parent.Description || "N/A",
      Type: parent.Type || "N/A",
      Revision: parent["Dropped Revision"] || "N/A",
      "Dropped Revision ID": parent["Dropped Revision ID"] || "N/A",
      "Connected Child Revision": parent.connectedChildRev || "N/A",
      "Latest child connected": parent.toBeChildRevConnected ? false : true,
      "To-Be child connected": parent.toBeChildRevConnected || "-",
      State: parent["Maturity State"] || "N/A",
      Owner: parent.Owner || "N/A",
      "CAD Format": parent["CAD Format"] || "N/A",
      Collabspace: parent["Collaborative Space Title"] || "N/A",
    };
     // Add relationship field if the type is Document
     if (item.Type === "Document") {
      rowData["relationship"] = parent.relationship || "N/A";
    }

  return rowData;
  });
};

export const tableColumns = [
  {
    accessorKey: "EIN",
    header: "EIN",
    columnPinning: true,
  },
  { accessorKey: "Title", header: "Title" },
  { accessorKey: "Description", header: "Description" },
  { accessorKey: "Type", header: "Type" },
  { accessorKey: "Revision", header: "Revision" },
  {
    accessorKey: "Connected Child Revision",
    header: "Connected Child Revision",
  },
  {
    accessorKey: "Latest child connected",
    header: "Latest child connected",
    cell: ({ cell }) => {
        const value = cell.getValue();
        // Render a checkmark if true, a cross if false
        return value ? (
            <CheckmarkCell />
        ) : (
            <CrossmarkCell />
        );
    },
},
  { accessorKey: "To-Be child connected", header: "To-Be child connected" },
  {
    accessorKey: "relationship",
    header: "Relationship",
  },
  { accessorKey: "State", header: "State" },
  { accessorKey: "Owner", header: "Owner" },
  { accessorKey: "CAD Format", header: "CAD Format" },
  { accessorKey: "Collabspace", header: "Collabspace" },
];


